.form-style {
    background:transparent;
	display: flex;
	flex-direction: column;
	align-items: center;
	/* width: 50%; */
	margin-top: 2rem;
	border-style: double ;
	border-width: 2px;
	border-color: transparent transparent gray transparent;
  
}


.tool-bar {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-content: center;
}

.tool-item {
	text-align: center;
	margin-left: 1rem;
	margin-right: 1rem;
	height: 2rem;
}

.select {
	width: 70px;
	margin-left: 10px;
}

.label {
	width: 70px;
	margin-left: 20px;
}


.submit {
	margin: 5px;	
	width: 100px;
}

.button-style {
	border-style: none;
	width: 50px;
	margin-left: 10px;
	background-color: transparent;
 }

.button-style:hover {
	color: teal;
  }
