	display: flex;
	flex-direction: column;
	align-items: center;
	/* width: 50%; */
	margin-top: 2rem;
	border-style: double ;
	border-width: 2px;
	border-color: transparent transparent gray transparent;
  
}


.tool-bar {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-content: center;
}

.tool-item {
	text-align: center;
	margin-left: 1rem;
	margin-right: 1rem;
	height: 2rem;
}

.select {
	width: 70px;
	margin-left: 10px;
}

.label {
	width: 70px;
	margin-left: 20px;
}


.submit {
	margin: 5px;	
	width: 100px;
}

.button-style {
	border-style: none;
	width: 50px;
	margin-left: 10px;
	background-color: transparent;
 }

.button-style:hover {
	color: teal;
  }


.cell-style {
    background: transparent;
    border: 1px solid #999;
    float: left;
    font-weight: bold;
    height: 50px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    width: 50px;
  }
  
  .cell-style input  {
    background: transparent;
    display: block;
    outline: 0;
    border: none;
    position: absolute;
    float: left;
    font-size: 24px;
    line-height: 36px;
    height: 36px;
    margin-right: 0 px;
    margin-top: 8px;
    padding: 0;
    text-align: center;
    width: 36px;
  }

  .cell-style input:focus  {
    color: #000000;
  }
  
  .cell-style  input:focus + label {
    color: #2c8efe;
  }
  
  
  .cell-style input + label {
    background:transparent;
    vertical-align: super;
    float: right; 
    font-size: 16px;
    padding: 0.2em;
    top: 16px;
    opacity: 0;
    color: #666;
    text-align: right;
    transition: top 0.7s ease, opacity 0.2s ease;
    margin-right:2px;
  }
  
  .cell-style input:valid + label {
    opacity: 1;
    top: 3px;
  }
