
.cell-style {
    background: transparent;
    border: 1px solid #999;
    float: left;
    font-weight: bold;
    height: 50px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    width: 50px;
  }
  
  .cell-style input  {
    background: transparent;
    display: block;
    outline: 0;
    border: none;
    position: absolute;
    float: left;
    font-size: 24px;
    line-height: 36px;
    height: 36px;
    margin-right: 0 px;
    margin-top: 8px;
    padding: 0;
    text-align: center;
    width: 36px;
  }

  .cell-style input:focus  {
    color: #000000;
  }
  
  .cell-style  input:focus + label {
    color: #2c8efe;
  }
  
  
  .cell-style input + label {
    background:transparent;
    vertical-align: super;
    float: right; 
    font-size: 16px;
    padding: 0.2em;
    top: 16px;
    opacity: 0;
    color: #666;
    text-align: right;
    transition: top 0.7s ease, opacity 0.2s ease;
    margin-right:2px;
  }
  
  .cell-style input:valid + label {
    opacity: 1;
    top: 3px;
  }