.App {
  text-align: center;
}

.mathdoku
{
  max-width: 50%;
  margin: 5rem;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-style: solid ;
  border-width: 1px;
  border-color: #909090;

}

html *
{
   font-family: 'Roboto' ;
}

.checkSolved {
  width:50%;
  margin-top: -10rem;
  margin-left: 25rem;
	justify-content: center;
}


.button-success,
.button-error,
.button-warning,
.button-secondary {
    color: white;
    border-radius: 4px;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}

.button-success {
    background: rgb(28, 184, 65);
    /* this is a green */
}

.button-error {
    background: rgb(202, 60, 60);
    /* this is a maroon */
}

.button-warning {
    background: rgb(223, 117, 20);
    /* this is an orange */
}

.button-secondary {
    background: rgb(66, 184, 221);
    /* this is a light blue */
}


h3 {
  font-weight: 100;
}

h5 {
  font-weight: 40;
}

/* LAYOUT CSS */

#layout {
  padding: 0;
}

.header {
  text-align: center;
  top: auto;
  margin: 3em auto;
}

.sidebar {
  background: rgb(61, 79, 93);
  color: #fff;
}

.brand-title,
.brand-tagline {
  margin: 0;
}
.brand-title {
  font-family: 'Caveat', cursive;
}
.brand-tagline {
  font-weight: 300;
  color: rgb(176, 202, 219);
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav-item {
  display: inline-block;
  *display: inline;
  zoom: 1;
}
.nav-item a {
  background: transparent;
  border: 2px solid rgb(176, 202, 219);
  color: #fff;
  margin-top: 1em;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-size: 85%;
}
.nav-item a:hover,
.nav-item a:focus {
  border: 2px solid rgb(61, 146, 201);
  text-decoration: none;
}

.content-subhead {
  text-transform: uppercase;
  color: #aaa;
  border-bottom: 1px solid #eee;
  padding: 0.4em 0;
  font-size: 80%;
  font-weight: 500;
  letter-spacing: 0.1em;
}

.content {
  padding: 2em 1em 0;
}

.post {
  padding-bottom: 2em;
}
.post-title {
  font-size: 1.6em;
  color: #222;
  margin-bottom: 0.2em;
}
.post-title-article {
  font-size: 1.5em;
  color: #222;
  margin-bottom: 0.1em;
}
.post-description {
  font-family: Georgia, "Cambria", serif;
  color: #444;
  line-height: 1.8em;
}
.post-meta {
  color: #999;
  font-size: 90%;
  margin: 0;
}

.post-category {
  margin: 0 0.1em;
  padding: 0.3em 1em;
  color: #fff;
  background: #999;
  font-size: 80%;
}
.post-category-design {
  background: #5aba59;
}
.post-category-pure {
  background: #4d85d1;
}
.post-category-yui {
  background: #8156a7;
}
.post-category-js {
  background: #df2d4f;
}


@media (min-width: 48em) {
.content {
  padding: 2em 3em 0;
  margin-left: 25%;
}

.header {
  margin: 80% 2em 0;
  text-align: right;
}

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
}

.footer {
  text-align: center;
}
}

.fetch-overlay {
  display: block;
  text-align: center;
  flex: 1;
  width: 50%;
  height: 50%;
  elevation: 4;
}

